import React, { lazy, Suspense } from 'react';
import { Switch, Route, Redirect, useLocation } from 'react-router-dom';
import { AnimatePresence } from 'framer-motion';

import Loader from './component/Loader/Loader';
import NavMotion from './layout/NavMotion';
import MainLayout from './layout/MainLayout';
import MinimalLayout from './layout/MinimalLayout';
import AddUser from './views/Users/AddUser';
import UserList from './views/Users/UserList';
import EditUser from './views/Users/EditUser';
import AddLoan from './views/Loans/AddLoan';
import Bill from './views/Bill/Bill';
import AllBills from './views/Billing/AllBills';
import EditLoan from './views/Loans/EditLoan';
import LoanList from './views/Loans/LoanList';
import AddInstallment from './views/Loans/AddInstallment';
import UserDetail from './views/Users/UserDetail';
import LoanDetails from './views/Loans/LoanDetails';
import Sale from './views/Sale/Sale';
import CashBook from './views/CashBook/CashBook';
import CashBookList from './views/CashBook/CashBookList';
import DownloadLoanReport from './views/Loans/DownloadLoanReport';
import EditInstallment from './views/Loans/EditInstallment';

const DashboardDefault = lazy(() => import('./views/Dashboard/Default'));

const Routes = () => {
    const location = useLocation();

    return (
        <AnimatePresence>
            <Suspense fallback={<Loader />}>
                <Switch>
                    <Redirect exact from="/" to="/dashboard" />
                    <Route path={[]}>
                        <MinimalLayout>
                            <Switch location={location} key={location.pathname}>
                                <NavMotion></NavMotion>
                            </Switch>
                        </MinimalLayout>
                    </Route>
                    <Route
                        path={[
                            '/register',
                            '/application/register',
                            '/forgot-password',
                            '/application/forgot-password',
                            '/pages/error/error1',
                            '/pages/error/error2',
                            '/pages/comingsoon',

                            '/dashboard',
                            '/customers/add-customer',
                            '/customers/detail/:id',
                            '/bill',
                            '/customers/grade-a',
                            '/customers/grade-b',
                            '/customers/grade-c',
                            '/customers/edit-customer/:id',
                            '/customers/list',
                            '/entries/add-entries',
                            '/entries/edit-entries/:id',
                            '/entries/detail/:id',
                            '/entries/list',
                            '/entries/monthly',
                            '/entries/add-installment',
                            '/entries/edit-installment/:id/:ins',
                            '/sale/reports',
                            '/entries/todays-installments',
                            '/entries/closed-entries',
                            '/cash-book',
                            '/cashbook-list',
                            '/entries/overdue',
                            '/entries/download-entries/:id',
                            '/entries/daily-overdue',
                            '/bills',
                        ]}
                    >
                        <MainLayout>
                            <Switch location={location} key={location.pathname}>
                                <NavMotion>
                                    <Route path="/dashboard" component={DashboardDefault} />
                                    <Route path="/customers/add-customer" component={AddUser} />
                                    <Route path="/bill" component={Bill} />
                                    <Route path="/bills" component={AllBills} />
                                    <Route path="/customers/detail/:id" component={UserDetail} />
                                    <Route path="/customers/edit-customer/:id" component={EditUser} />
                                    <Route path="/customers/list" component={UserList} />
                                    <Route path="/entries/add-entries" component={AddLoan} />
                                    <Route path="/entries/edit-entries/:id" component={EditLoan} />
                                    <Route path="/entries/list" component={LoanList} />
                                    <Route path="/entries/detail/:id" component={LoanDetails} />
                                    <Route path="/entries/add-installment/:id" component={AddInstallment} />
                                    <Route path="/entries/edit-installment/:id/:ins" component={EditInstallment} />
                                    <Route path="/sale/reports" component={Sale} />
                                    <Route path="/cash-book" component={CashBook} />
                                    <Route path="/cashbook-list" component={CashBookList} />
                                    <Route path="/entries/download-entries/:id" component={DownloadLoanReport} />
                                </NavMotion>
                            </Switch>
                        </MainLayout>
                    </Route>
                </Switch>
            </Suspense>
        </AnimatePresence>
    );
};

export default Routes;
